header {
	display: flex;
	flex-direction: column;
	height: 5em;
	background-color: #00bcd4;
}

.material-icons {
	font-size: 2.4em;
	cursor: pointer;
}

#root {
	border: solid #00bcd4;
}

.nav-wrapper {
	display: flex;
	justify-content: space-between;
	padding: 2%;
	width: 90%;
}

.nav-wrapper>ul {
	display: flex;
	list-style-type: none;
	padding-right: 3em;
	margin-block-start: 0px;
}

.nav-wrapper>ul>li {
	padding-right: 0.5em;
	color: white;
	text-decoration: none;
}

.card {
	text-align: center;
	padding: .5em;
	margin: 1em;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
	transition: 0.3s;
	width: 15%;
	border: 1px solid #00bcd4;
	border-radius: 5px;
}

.card:hover {
	box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

img {
	border-radius: 5px 5px 0 0;
}


/* .container {
  padding: 2px 16px;
  border: solid #2196F3;
} */

.primaryButton {
	display: inline-block;
	padding: 10px 20px;
	margin-right: 1em;
	font-size: 1em;
	font-weight: bold;
	cursor: pointer;
	text-align: center;
	text-decoration: none;
	outline: none;
	color: #fff;
	background-color: #00bcd4;
	border: none;
	border-radius: 15px;
}

.primaryButton:hover {
	background-color: #2196F3
}

.checkOut {
	display: flex;
	margin: 1em;
	justify-content: space-between;
}

.checkOut>img {
	padding: 2em;
}

.checkOut>.bookContent>div {
	font-weight: 700;
	line-height: 2em;
	min-width: 4em;
	padding-bottom: 1em;
}

.bookContent {
   width: 25em;
}

textarea {
	min-width: 20em;
	font-weight: 700;
	line-height: 2.5em;
	width: 90%;
}

.buttonFooter {
	display: flex;
	flex-direction: row;
}

.myContainer {
	width: 98%;
	max-height: 20%;
	margin: 10px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
	-moz-box-shadow: 0px 2px 7px #292929;
	-webkit-box-shadow: 0px 2px 7px #292929;
	border-radius: 10px;
	-moz-border-radius: 10px;
	-webkit-border-radius: 10px;
}

.mainbody,
.header {
	padding: 5px;
	margin: 1em;
}

.myMainbody {
	margin-top: 0;
	padding: 1em;
	min-height: 150px;
	max-height: 388px;
	overflow: auto;
	display: flex;
	font-weight: 700;
	line-height: 2em;
	min-width: 4em;
	padding-bottom: 1em;
}

.myHeader {
	display: flex;
	justify-content: space-between;
	height: 2em;
	padding: 1em;
	color: #fff;
	background-color: #86d8e2;
	-webkit-border-top-left-radius: 5px;
	-webkit-border-top-right-radius: 5px;
	-moz-border-radius-topleft: 5px;
	-moz-border-radius-topright: 5px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}

.flexRow {
	width: 50%;
	flex-direction: row;
}

.tableParent {
	width: 50%;
}

table {
	width: 95%;
}

table,
tr,
td,
th {
	padding: 0.5em;
	border-collapse: collapse;
	border: 1px solid;
}

td:last-child,
td:nth-last-child(2) {
	text-align: center;
}

table>button {
	padding: 5px 10px;
}

.billFields {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

@media screen and (max-width: 768px) {
	body {
		margin-top: 0;
	}
	.checkOut {
		display: block;
		margin: 1em;
		width: 95%;
	}
	.flexRow {
		display: block;
		width: 95%;
	}
	.tableParent {
		width: 95%;
	}
	.box {
		display: flex;
		justify-content: space-around;
		flex-wrap: wrap;
	}
	.nav-wrapper {
		display: block;
		justify-content: space-between;
		padding: 2%;
		width: 90%;
	}
	.nav-wrapper>ul {
    list-style-type: none;
    padding-top: 10px;
		margin: 10px 0px 0px 0px;
    padding: 0;    
	}
}